	outline: dotted 2px;
}

#accessibilitytoolbarGraphic .uci-onoffswitch-label,
#accessibilitytoolbarGraphic .uci-onoffswitch-label-on {
	display: block;
	overflow: hidden;
	cursor: pointer;
	float: left;
	box-sizing: content-box;
}

.uci-onoffswitch-label, .uci-onoffswitch-label-on {
	border: 2px solid #666;
	display: block;
	height: 1.625em;
	margin: 0;
	padding: 0 10px;
	position: relative;
	width: 2.25em;
}

